




























import Vue from 'vue';
import api from '../services/api';
import axios from 'axios';
import router  from '../router/index'
import i18n from '../services/lang';

export default Vue.extend({
  name: 'Lasttexts',
  data(){
   return{ lasttexts:[],Lang:this.$store.getters.GetLang
}
  },
  
 watch:{
    Lang: function (val) {
           return  i18n.locale = val;

}
 },
 async created()
  {
 await axios({
    method: 'get',
    url: api.apiurl+'getLastTexts',
   
    })
.then(res=>
{
    console.log(res.data.lastTexts)

if(res.data.lastTexts)
{
  this.lasttexts=res.data.lastTexts
  
}
else
{
 this.$root.$bvToast.toast("Son yazlar getirilemedi", {
          title: 'Baarsz',
          autoHideDelay: 5000,
          appendToast: true,
          variant: 'danger',

        })
}
}).catch(err=>{
   
        this.$root.$bvToast.toast("Son yazlar getirilemedi", {
          title: 'Baarsz',
          autoHideDelay: 5000,
          appendToast: true,
          variant: 'danger',

        })
        
          });
  },
  methods:{
hrefArticleDetails(baslik: any)
{

  //this.$root.$router('/articledetails/'+encodeURIComponent(baslik));
  // router.push('/articledetails/'+encodeURIComponent(baslik))
      router.push('/articledetails?baslik='+baslik)
    // router.forward()
}
     
  }
});
