   return{ showinput:false,ishome:false,isabout:false}
  },
  created()
  {
    console.log(this.$route.path)
   if(this.$route.path=='/')//home yada about olur.
   {
     console.log('h')
    this.ishome=true;

   }
   else if(this.$route.path=='/about')//home yada about olur.
   this.isabout=true;
   //this.$emit('Search2Channel',false);
  },
  methods:{
